@import "utils";
@import "fonts";
@import "typography";

body {
  background: $c-gray;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.wrapper {
  height: 190vh;
}
.header__content {
  top: 0;
  width: 95%;
  height: 500px;
  background-color: #56ccf2;
  clip-path: polygon(0 0, 100% 0, 95% 96%, 0 100%);
  position: relative;
  padding: 0 10%;
  padding-top: 30px;
  position: relative;
}

.navigation__menu {
  /* position: absolute; */
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 50px;
  align-items: center;
}

.navigation-list {
  display: flex;
  justify-content: space-around;
  text-decoration: none;

}
.navigation-list li {
  list-style-type: none;
  /* margin: 0 33px; */
}
.nav-link {
  text-decoration: none;
  font-family: Muli, sans-serif;
  font-style: normal;
  font-weight: bold;
  /* font-size: 16px; */
  line-height: 150%;
  color: #FFFFFF;
}

.logo {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: bold;
font-size: 32px;
line-height: 100%;
color: #fff;
width: 15%;
}

.nav {
  width: 55%;
}

.hire-link {
  width: 30%;
  text-align: center;
}

.agency {
  padding: 30px 0 0 79px;
}

.agency-name {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: 900;
font-size: 24px;
line-height: 150%;
color: #fff;
}

.agency-description {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: normal;
font-size: 17px;
line-height: 150%;
color: #fff;
}

.relevant__button {
width: 150px;
height: 40px;
background: #fff;
border-radius: 64px;
color: #253757;
text-align: center;
letter-spacing: 1px;
border: 1px solid #fff;
font-family: Muli, sans-serif;
font-style: normal;
font-weight: bold;
font-size: 12px;
line-height: 150%;
margin-top: 10px;
}

.header-photo {
  /* position: absolute; */
  /* display: block; */
  /* float: left; */
  /* text-align: center; */
  /* top: 100px; */
}



.header__photo {
  width: 75%;
  height: auto;
  position: absolute;
  /* display: block; */
  /* float: left; */
  /* text-align: center; */
  top: 380px;
  left: 136px;
}

.header__photo img::after {
  content: "";
  display: block;
  width: 100%;
  clear: both;
}

.welcom__container {
  width: 40%;
  height: 150px;
  margin-left: 400px;
}

.welcom__container img {
  float: left;
  width: 20px;
  height: 20px;
  margin-right: 16px;
}

.header-welcome {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: 800;
font-size: 16px;
line-height: 150%;
align-items: center;
letter-spacing: 2px;
text-transform: uppercase;
color: #56CCF2;
/* float: left; */
}

/* .header-welcome::before {
  background-image: url("images/grid.svg");
  width: 20px;
  height: 20px;
} */

.header-welcome__text {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: 600;
font-size: 15px;
line-height: 150%;
color: #253757;
float: left;
}

.company-role {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: 900;
font-size: 40px;
line-height: 100%;
text-align: center;
color: #253757;
}

.container {
padding: 10px 66px 20px 138px;
}

.company-role__sections {
  display: flex;
  justify-content: flex-start;
  padding-top: 70px;
}

.role-section img {
  width: 160px;
  height: 160px;
}

.role-section {
  padding: 5px 100px 0 15px;


}

.role-title {
  font-family: Muli, sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 19.6px;
  line-height: 140%;
  color: #253757;

}

.role-text {

  font-family: Muli, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 150%;
  color: #334563;
  mix-blend-mode: normal;
}

.process-text img {
  float: left;
  margin-right: 10px;
}

.process-block {
  display: flex;
}

.process-text {
  width: 30%;
}

.process-number__blocks {
  width: 60%;

  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.number {
  width: 240px;
  height: 256px;
  background: #FFFFFF;
box-shadow: 16px 16px 16px rgba(0, 0, 0, 0.02);
display: flex;
flex-direction: column;

}

.number p {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: normal;
font-size: 12.6px;
line-height: 150%;
text-align: center;
color: #253757;
mix-blend-mode: normal;
}

.order {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: 900;
font-size: 40px;
line-height: 150%;
text-align: center;
letter-spacing: 2px;
text-transform: uppercase;


position: relative;
margin-top: 15px;
}

.one::before {
