opacity: 0.1; */

}

.circle {
  width: 48px;
  height: 48px;
  border-radius: 50%;

  opacity: 0.5;

  align-self: center;
  /* order: 1; */
  position: absolute;
}





.one {
  color: #56ccf2;
}

.two {
  color: #fcc304;
}

.three {
  color: #03d542;
}

.four {
  color: #f25656;
}

.features {
  width: 100%;
  height: 976px;
  background: #56ccf2;
  background: url("./images/office-photo.png") no-repeat right, #56ccf2;
}

.features-name {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: 800;
font-size: 16px;
line-height: 150%;
align-items: center;
letter-spacing: 2px;
text-transform: uppercase;
color: #fff;
}

.features-text {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: normal;
font-size: 16px;
line-height: 150%;
color: #fff;
mix-blend-mode: normal;
}

.person {
  width: 314px;
height: 464px;
background: #fefefe;
box-shadow: 16px 16px 32px rgba(0, 0, 0, 0.05);
display: flex;
flex-direction: column;
}

.person-color {
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, rgba(255, 255, 255, 0) 100%);
  /* background-color: #56CCF2; */
border-radius: 500px;
transform: rotate(25deg);
height: 162px;
width: 64px;
/* background-image: url("images/person1.png"); */
align-self: center;
}

.blue {
  background-color: #56ccf2;

}

.yellow {
  background-color: #fcc304;
}

.green {
  background-color: #03d542;
}

.red {
  background-color: #f25656;
}
.person-color img {
  transform: rotate(-22deg);
  margin-top: 50px;
}

.quotation {
  font-family: Muli, sans-serif;
font-style: italic;
font-weight: normal;
font-size: 14.4px;
line-height: 150%;
text-align: center;
color: #253757;
mix-blend-mode: normal;
}

.divide {
  width: 64px;
height: 2px;
background:  #56ccf2;
border-radius: 500px;
transform: matrix(1, 0, 0, -1, 0, 0);
border: 1px solid #56ccf2;
}

.person-name {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: bold;
font-size: 12.6px;
line-height: 150%;
text-align: center;
color: #152032;
}

.person-position {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: 800;
font-size: 12.6px;
line-height: 150%;
text-align: center;
letter-spacing: 2px;
text-transform: uppercase;
color: #56CCF2;
}

.vision {
  width: 100%;
  height: 700px;
  text-align: center;
  position: relative;
}

.vision-title {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: 900;
font-size: 48px;
line-height: 100%;
text-align: center;
color: #253757;
}

.vision-text {
  font-family: Muli, sans-serif;
font-style: normal;
font-weight: 600;
font-size: 24px;
line-height: 150%;
text-align: center;
color: #334563;
}

.primary-button {
  width: 226px;
height: 56px;
background: #2F80ED;
border-radius: 64px;
border: 2px solid #2F80ED;
font-family: Muli, sans-serif;
font-style: normal;
font-weight: bold;
font-size: 16px;
line-height: 150%;
text-align: center;
letter-spacing: 1px;
color: #fff;
}

